@import '_variables';
.btn {
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 1px;
    word-spacing: 4px;
    padding: 8px 15px;

	&-secondary {
		background-color: $secondary;
		border-color: $secondary;
		color: #fafbfb;

		&:hover,
		&:active {
			background-color: #d25e01;
		}
	}
}