@import "../templates/variables";
@import "../templates/mixins";

.modal {
    &-dialog {
        max-width: 100%;

        @include responsive(desktop) {
            max-width: 975px;
            
        }
    }
    
    &-content {
        padding: 20px 45px;
    }
    &-header {
        border-bottom: 0;
    }
    &-title {
        color:  $secondary;
        font-size: 48px;/* Приближение из-за подстановки шрифтов */
        font-weight: 300;
        text-transform: uppercase;
    }
    .close {
        color: $border-color-primary;
        background: none;
        border: none;
        -webkit-appearance: unset;
        margin-top: -8px;
    }
}