@import '_variables';
@import "_mixins";

body {
	font-family: "AvenirNext";
	font-weight: 400;
	font-size: 23px;
	color: $text-color;
}
section {
	padding: 40px 0; 
}
.container {
	max-width: initial; 
	
	@include responsive(desktop) {
		max-width: 1200px;
	}
}

h1 {
	color:  #0066a6;
	font-size: 57px;/* Приближение из-за подстановки шрифтов */
	font-weight: 400;
	margin-top: 60px;
	margin-bottom: 60px;
	text-transform: uppercase;

}
h2 {
	color:  $primary;
	font-size: 48px;/* Приближение из-за подстановки шрифтов */
	font-weight: 400;
	text-transform: uppercase;
	margin-top: 60px;
	margin-bottom: 60px;
}
p {
	line-height: 130%;
	margin-bottom: 11px;
}
a {
	text-decoration: underline;

	&:not(.link-primary) {
		color: $text-color;
	}

	&:hover,
	&:active {
		text-decoration: none;
	}
}


.logo {
	display: inline-block;
	margin-right: 10px;
	background-size: cover;
	background-position: top left;
	background-repeat: no-repeat;
}
.main-nav {
	@include gradient-x(#cdd8de, #add6ec);
	

	&-list {
		display: flex;
		padding-left: 0;
		margin-bottom: 0;
		justify-content: space-between;

		&__element {
			display: block;
			padding: 20px 10px;

			&:first-child {
				padding-left:0;
			}
			&:last-child {
				padding-right:0;
			}

			a {
				color:  $primary;
				font-size: 14px;/* Приближение из-за подстановки шрифтов */
				font-weight: 300;	
				text-transform: uppercase;
				text-decoration: none;

				@include responsive(desktop) {
					font-size: 17px;
				}
			}
		}
	}
}